<template>
    <el-dialog class="add-org-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm">
        <el-form ref="dialogForm" :model="dialogForm" :rules="orgRules" label-width="120px">
            <el-row type="flex" :gutter="15">
              <el-col :span="8">
                <el-form-item label="被实施地区" prop="implArea">
                    <el-input v-model="dialogForm.implArea" placeholder="请填写被实施地区"></el-input>
                </el-form-item>
                <el-form-item label="实施主体层级" prop="implBodyHierarchy">
                    <el-select placeholder="请选择实施主体层级" clearable v-model="dialogForm.implBodyHierarchy">
                        <el-option v-for="(item, index) in dic['机构层级']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="被实施层级" prop="implAreaHierarchy">
                    <el-select placeholder="请选择被实施层级" clearable v-model="dialogForm.implAreaHierarchy">
                        <el-option v-for="(item, index) in dic['机构层级']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="政策种类" prop="policyType">
                    <el-select placeholder="请选择政策种类" clearable v-model="dialogForm.policyType">
                        <el-option v-for="(item, index) in dic['政策种类']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="实施主体名称" prop="implBodyName">
                    <el-input v-model="dialogForm.implBodyName" placeholder="请填写实施主体名称"></el-input>
                </el-form-item>                
              </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/zz_service.js";

export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      dic: {
        机构层级: [],
        政策种类: []
      },
      dialogForm: {
        districtCode: ""
      },
      orgRules: {
        implArea: [
          { required: true, message: "请输入被实施地区", trigger: "blur" },
          { min: 2, max: 25, message: "长度在 2 到 25 个字符", trigger: "blur" }
        ],
        implAreaHierarchy: [
          { required: true, message: "请选择被实施层级", trigger: "change" }
        ],
        implBodyName: [
          { required: true, message: "请输入实施主体", trigger: "blur" },
          { min: 2, max: 25, message: "长度在 2 到 25 个字符", trigger: "blur" }
        ],
        implBodyHierarchy: [
          { required: true, message: "请选择实施主体层级", trigger: "change" }
        ],
        policyType: [
          { required: true, message: "请选择政策种类", trigger: "change" }
        ]
      }
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },

  
  methods: {
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.dicMap && delete param.dicMap;
          if (this.isAdd == true) {
            // 添加
            service.requestSaveLeaderDuty(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "综治领导责任添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateLeaderDuty(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "综治领导责任修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.dialogForm = {
        districtCode: ""
      };
    }
  }
};
</script>
<style>
.el-date-editor.el-input {
  width: 100%;
}
</style>